import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
const layout = () => import('@/pages/layout')
const aboutMe = () => import('@/pages/aboutMe')
const shareContent = () => import('@/pages/shareContent')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'layout',
      redirect: '/aboutMe',
      component: layout,
      children: [
        {
          name: 'aboutMe',
          path: 'aboutMe',
          component: aboutMe,
          meta: {
            parentTitle: '关于我'
          }
        },
        {
          name: 'shareContent',
          path: 'shareContent',
          component: shareContent,
          meta: {
            parentTitle: '分享'
          }
        }
      ]
    }
  ]
})



